import React, { useState, useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import { Pagination, Autoplay } from 'swiper/modules';
import { Link } from 'react-router-dom';
import MainStyles from './Main.module.css';

const Main = () => {
    const [isMobile, setIsMobile] = useState(false);

    useEffect(() => {
        const handleResize = () => {
            setIsMobile(window.innerWidth <= 768);
        };

        window.addEventListener('resize', handleResize);
        handleResize(); //      

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    return (
        <div className={MainStyles.main}>
            <div className={MainStyles.SwiperSlide}>
                <Swiper
                    modules={[Pagination, Autoplay]}
                    spaceBetween={50}
                    slidesPerView={1}
                    navigation
                    pagination={{ clickable: true }}
                    autoplay={{
                        delay: 2000,
                        disableOnInteraction: false,
                    }}
                    speed={1000} //    1000ms 
                    allowTouchMove={false}
                    loop={true} //   
                >
                    <SwiperSlide>
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-1.png' : '/images/1.png')}
                            alt="Image 1"
                        />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-2.png' : '/images/2.png')}
                            alt="Image 2"
                        />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-3.png' : '/images/3.png')}
                            alt="Image 3"
                        />
                    </SwiperSlide>
                </Swiper>
            </div>
            <div className={MainStyles.product}>
                <h1>Product</h1>
                <div className={MainStyles.product_container}>
                    <Link to="/product/robot">
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-.png' : '/images/.png')}
                            alt=""
                        />
                        <p>
                            Robot
                            <span></span>
                        </p>
                    </Link>

                    <Link to="/product/PC">
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-.png' : '/images/.png')}
                            alt=""
                        />
                        <p>
                            PC
                            <span></span>
                        </p>
                    </Link>

                    <Link to="/product/Etc">
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-.png' : '/images/.png')}
                            alt=""
                        />
                        <p>A/V Products</p>
                    </Link>

                    <Link to="/product/Electronics">
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-.png' : '/images/.png')}
                            alt=""
                        />
                        <p>
                            Electronics
                            <span></span>
                        </p>
                    </Link>

                    <Link to="/product/Living">
                        <img
                            src={process.env.PUBLIC_URL + (isMobile ? '/images/m-.png' : '/images/.png')}
                            alt=""
                        />
                        <p>
                            Living
                            <span></span>
                        </p>
                    </Link>
                </div>
            </div>
        </div>
    );
};

export default Main;
